
.menus {
  color: #999;
  font-size: 16px;
  width: 200px;
  height: 100%;
  background: #202020;
  position: fixed;
  left: 0;
  top: 0;
  overflow: scroll;
  -webkit-transition: all 1s;
  transition: all 1s;
  text-align: center;
}
.menus .aside .active {
  color: #fff;
  background: #1a1a1a;
}
.menus .user {
  height: 230px;
  width: 100%;
}
.menus .user img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin: 30px 20px 10px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.menus .user img:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.menus li {
  height: 45px;
  margin: 15px 0;
}
.menus li a {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.menus li a:hover {
  color: #fff;
}


