
.view {
  text-align: center;
}
.view .box {
  padding: 1px;
  display: inline-block;
}
.view .box img {
  width: 300px;
}
.view .loadMore {
  width: 200px;
  height: 30px;
  font-size: 14px;
  color: #fff;
  background: #ed4040;
  border: 1px solid #ed4040;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  margin: 40px 0;
}


