
.music {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
  text-align: center;
}
.music .bg {
  height: 100%;
  width: 100%;
  -webkit-filter: blur(100px);
          filter: blur(100px);
  position: absolute;
  left: 0;
  top: 0;
}
.music .song {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 50px;
  z-index: 99;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.music .song .msg {
  color: #d9d8d8;
  margin: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.music .song .msg .name {
  padding-left: 15px;
}
.music .song .list {
  height: 450px;
  width: 800px;
  overflow-y: scroll;
  font-size: 14px;
  text-align: left;
}
.music .song .list li {
  cursor: pointer;
  height: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}
.music .song .list li:hover .playBtn {
  display: block;
}
.music .song .list .name {
  width: 60%;
}
.music .song .list .singer {
  width: 30%;
}
.music .song .list .time {
  width: 10%;
}
.music .song .list .playBtn {
  height: 40px;
  width: 60px;
  position: absolute;
  left: 45%;
  top: 0;
  z-index: 999;
  overflow: hidden;
}
.music .song .list .playBtn img {
  width: 100%;
  height: 40px;
  -webkit-filter: blur(40px);
          filter: blur(40px);
}
.music .song .list .playBtn span {
  position: absolute;
  color: #fff;
  font-size: 16px;
  top: 0;
  left: 0;
  display: inline-block;
  height: 40px;
  line-height: 40px;
  text-indent: 15px;
}
.music .song .lyric {
  width: 500px;
  height: 600px;
  color: #fff;
/*border: 1px solid #eee;*/
}
.music .song .lyric img {
  height: 200px;
  width: 200px;
}
.music .song .lyric p {
  margin: 15px;
}
.music .play {
  position: absolute;
  bottom: 10%;
  left: 10%;
  z-index: 99;
}
.music .play audio {
  height: 80px;
  width: 800px;
}


