
.movie {
  width: 100%;
}
.movie .top {
  height: 60px;
  width: 100%;
  display: inline-block;
  color: #37a;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 25px;
  background-color: #f0f3f5;
}
.movie .main {
  text-align: center;
}
.movie .main .item {
  height: 444px;
  width: 300px;
  display: inline-block;
  margin: 10px 5px 5px 10px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}
.movie .main .item .title {
  height: 40px;
  width: 100%;
  color: #fff;
  font-size: 18px;
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: rgba(0,0,0,0.5);
}
.movie .main .item img {
  height: 100%;
  width: 100%;
}
.movie .main .item .msg {
  width: 100%;
  height: 200px;
  color: #fff;
  background: rgba(0,0,0,0.5);
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: left;
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-transform: translateY(90px);
          transform: translateY(90px);
}
.movie .main .item .msg p {
  padding: 5px 12px;
}
.movie .main .item .loadMore {
  height: 30px;
  width: 200px;
  background-color: #ed4040;
  border: 1px solid #ed4040;
  border-radius: 4px;
}
.movie .main .item:hover .msg {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}


