
.welcome {
  height: 100%;
  width: 100%;
  background: url("https://daoket.github.io/resume/images/blog/welcome.jpg") no-repeat;
  background-size: 100% 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.welcome .dialog {
  width: 550px;
  overflow: hidden;
  background: #fff;
  border-radius: 5px;
  text-align: center;
  padding-bottom: 30px;
  opacity: 0.8;
  -webkit-animation: shake 0.3s 1 linear;
          animation: shake 0.3s 1 linear;
}
.welcome .dialog img {
  width: 100%;
}
.welcome .dialog h1 {
  font-size: 30px;
  margin: 20px 0;
  font-weight: bold;
}
.welcome .dialog p {
  padding: 8px 12px;
  font-size: 18px;
}
@-webkit-keyframes shake {
0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
25% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}
50% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
75% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}
100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
}
@keyframes shake {
0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
25% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}
50% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
75% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}
100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
}
@media only screen and (max-width: 800px) {
.welcome .dialog {
    width: 80%;
}
.welcome .dialog p {
    font-size: 13px;
}
}


