
html,
body,
#app {
  height: 100%;
  margin: 0;
  padding: 0;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 200px;
  -webkit-transition: all 1s;
  transition: all 1s;
}
#app .open {
  display: none;
  color: #fff;
  height: 30px;
  width: 30px;
  padding: 10px;
  cursor: pointer;
  position: fixed;
  top: 0;
  z-index: 999;
  left: 200px;
  line-height: 100px;
  cursor: pointer;
  background: rgba(0,0,0,0.2);
}
#app .open .line {
  height: 1px;
  width: 30px;
  background-color: #fff;
  margin-bottom: 10px;
}
@media only screen and (max-width: 800px) {
#app {
    padding-left: 0;
}
#app .open {
    display: block;
    left: 0;
}
#app .load {
    left: 0;
}
.hide {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.hide .show {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
}


